@mixin child-not-last {
    >*:not(:last-child) {
        @content;
    }
}

@mixin static-width($width) {
    min-width: $width;
    width: $width;
}

@mixin static-size($size) {
    @include static-width($size);
    height: $size;
}

@mixin hover {

    @media (hover: hover) {
        &:hover {
            @content;
        }
    }


    @media (hover: none) {
        &:active {
            @content;
        }
    }

}

@mixin hover-focus {
    &:focus {
        @content;
    }

    @include hover {
        @content;
    }
}